import uuid from 'uuid';

// ADD_EXPENSE
export const addExpense = (
  {
    description = '',
    note = '',
    amount = 0,
    createdAt = 0
  } = {}
) => {
  const id = uuid();
  return {
    type: 'ADD_EXPENSE',
    expense: {
      id,
      description,
      note,
      amount,
      createdAt
    }
  };
};

// REMOVE_EXPENSE
export const removeExpense = ({ id } = {}) => ({
  type: 'REMOVE_EXPENSE',
  id
});

// EDIT EXPENSE
export  const editExpense = (id, updates) => ({
  type : 'EDIT_EXPENSE',
  id,
  updates
});


